import React from "react";
import {Tab, TabsBar, TabContent, Input, InlineField, Select,Button} from '@grafana/ui';

interface Props {
    onSubmit: Function
}

interface States extends Record<string, any>{
    selectedIndex: number
    distance: string
    lb_min:number
    lb_max:number
    k_g:number
    k_i:number
}
const tabs = [{label:'Leader Bin',index:0},{label:'K-mean',index:1}]
export class ClusterControl extends React.Component<Props, States> {
    constructor(props:Props) {
        super(props);
        this.state={
            selectedIndex:0,
            distance: 'l1',
            lb_min:9,
            lb_max:10,
            k_g:8,
            k_i:10
        }
    }
    render(): React.ReactElement<any, string | React.JSXElementConstructor<any>> | string | number | {} | React.ReactNodeArray | React.ReactPortal | boolean | null | undefined {
        const {selectedIndex,lb_min,lb_max,k_g,k_i,distance} = this.state;
        return <div>
                    <TabsBar>
                        {tabs.map((tab) => {
                            return (
                                <Tab
                                    css
                                    key={tab.index}
                                    label={tab.label}
                                    active={tab.index===selectedIndex}
                                    onChangeTab={() => this.setState({selectedIndex:tab.index})}
                                    // counter={(index + 1) * 1000}
                                />
                            );
                        })}
                    </TabsBar>
                    <TabContent>
                        <InlineField label="Distance Function">
                            <Select
                                onChange={(val)=>this.setState({distance:''+val.value})}
                                value={distance}
                                options={[
                                { value: 'l1', label: 'L1 - Manhattan' },
                                { value: 'l2', label: 'L2 - Euclidean' },
                            ]}/>
                        </InlineField>
                        <div style={{display:'flex', marginRight:6}}>
                            {(tabs[0].index===selectedIndex) && <>
                                <InlineField label="#Groups from" style={{width:150}}>
                                    <Input css type={"number"} value={lb_min} onChange={v=>this.setState({lb_min:+v.currentTarget.value})}/>
                                </InlineField>
                                <InlineField label="to" style={{width:80}}>
                                    <Input css type={"number"} value={lb_max} onChange={v=>this.setState({lb_max:+v.currentTarget.value})}/>
                                </InlineField>
                            </>}
                            {(tabs[1].index===selectedIndex) && <>
                                <InlineField label="#Groups" style={{width:110}}>
                                    <Input css type={"number"} value={k_g} onChange={v=>this.setState({k_g:+v.currentTarget.value})} />
                                </InlineField>
                                <InlineField label="#Steps" style={{width:120}}>
                                    <Input css type={"number"} value={k_i} onChange={v=>this.setState({k_i:+v.currentTarget.value})} />
                                </InlineField>
                            </>}
                            <Button fullWidth onClick={()=>this.props.onSubmit(selectedIndex===0?
                                {clusterMethod:'leaderbin',bin:{startBinGridSize:10,range:[lb_min,lb_max]},normMethod:distance}:
                                {clusterMethod:'kmean',bin:{k:k_g,iterations:k_i},normMethod:distance})}>Calculate cluster</Button>
                        </div>
                    </TabContent>
                </div>;
    }
}
