import {DataArr, RadarData, undefinedValue} from "../Ulti";
import {Dimension} from "../MainViz";
import {Cluster} from "./cluster";
// @ts-ignore
import _ from "lodash";

// @ts-ignore
const scagnosticsnd = require("../dirjs/scagnosticsnd.min");
export interface OutlierGroup extends Array<Cluster>{
    pointObject: Record<string, RadarData>,
    missingData: Record<string, DataArr>,
}

export function outlier(data: DataArr[],dimensions:Dimension[]):OutlierGroup{
    console.time('outline:');
    let dataSpider3: DataArr[] = [];
    const missingData: Record<string, DataArr> = {}
    data.forEach(d=>{

        if (d.findIndex(e=>!_.isNumber(e))>-1){
            d.outlier = 2; //missing
            d.cluster = 'missing';
            missingData[d.name+'_'+d.timestep] = (d);
        }else{
            delete  d.outlier
            dataSpider3.push(d);
        }
    })
    let estimateSize = Math.max(1, Math.pow(500, 1 / dataSpider3[0].length));
    console.log('estimateSize:',estimateSize);
    let scagOptions ={
        // isNormalized: true,
        startBinGridSize: estimateSize,
        // minBins: 20,
        // maxBins: Math.sqrt(dataSpider3.length),
        outlyingCoefficient: 1.5,
        // incrementA:2,
        // incrementB:0,
        // decrementA:1 / 3,
        // decrementB:0,
    };

    // @ts-ignore
    let outlyingBins: OutlierGroup = [];
    outlyingBins.pointObject = {};
    outlyingBins.missingData = missingData;
    debugger
    // remove outlying
    scagnosticsnd
    // @ts-ignore
    let scag = self.scagnosticsnd(dataSpider3.map((d, i) => {
        var dd = d.map(d => d ?? undefinedValue);
        // @ts-ignore
        dd.data = d;
        delete  d.outlier;
        return dd;
    }), scagOptions);
    console.timeEnd('outline:');
    console.log('Total bin=' + scag.bins.length);
    console.log('Outlying bin=' +scag.outlyingBins.length);



    scag.outlyingBins.map((ob:any,i:number)=>{
        // @ts-ignore
        let temp2:Cluster = {labels: -i-1,compObject:{}};

        let arr = ob.map((o:any)=>{
            let d = o.data;
            d.outlier = 1;
            d.cluster = 'outlier';
            d.clusterIndex = -i-1;
            let temp : RadarData = dimensions.map((s,si)=>({axis:s.text, value: d[si],angle: s.angle as number}));
            temp.name = d.name+'_'+d.timestep;
            temp.nodeName = d.name;
            temp.timestep = d.timestep;
            temp.cluster =  'outlier';
            temp.clusterIndex =  -i-1;
            outlyingBins.pointObject[temp.name] = temp;
            if (!temp2.compObject[d.name]){
                temp2.compObject[d.name] = []
            }
            temp2.compObject[d.name].push(d);
            return outlyingBins.pointObject[temp.name];
        });

        ob.site.forEach((s:number, i:number) => temp2[dimensions[i].text] = dimensions[i].scale.invert(s));
        temp2.index = -i-1;
        // @ts-ignore
        temp2.__metrics = dimensions.map((s,si)=>({axis:s.text, value: ob.site[si], angle: s.angle as number}));
        temp2.__metrics.normalize = ob.site.slice();
        temp2.arr = arr;
        outlyingBins.push(temp2);
    });
    console.log('#Outlier: ',Object.values(outlyingBins.pointObject).length)
    debugger
    return outlyingBins;
}
