import React from "react";
import {DataFrame, Field, PanelProps, Vector} from "@grafana/data";
import {DisplayOpt, SimpleOptions} from 'types';
import {css} from 'emotion';
// import {css} from 'emotion';
// import {stylesFactory} from '@grafana/ui';
import Sankey, {JobOBject, SankeyInput, Link, getUserName2, CompSelectNode,CompSelect} from './Sankey'
import {schemeData, CompOBject, UserOBject, DataArr, SummaryArr, TSNEDATA, RadarData} from './Ulti'
// import{Timeline} from './Timeline'
import * as d3 from 'd3';
import {ScaleLinear, ScaleOrdinal, ScaleSequential} from "d3-scale";
import * as _ from "lodash"
// import { stylesFactory} from "@grafana/ui";
import {InlineField, Select, Slider, ThemeContext} from '@grafana/ui';
import PieChart from "./PieChart";
import { Drawer } from 'drawer';
import LineChart, {LineInput} from 'lineChart';
import {Binopt, calculateCluster, Cluster} from 'cluster/cluster';

// import { StreamLegend } from 'StreamLegend';
import { AppEvents } from '@grafana/data';
// @ts-ignore
import appEvents from 'grafana/app/core/app_events';
import { ClusterControl } from 'cluster/clusterControl';
import { Radar } from 'cluster/radar';
import {outlier, OutlierGroup} from "./cluster/outlier";
// import { calculateCluster } from 'cluster/cluster';

interface DataObject extends Record<string,Date|number|string|undefined|null> {
    order: number
};
type DataByTime = Record<string,Record<string,DataObject>>;

interface Props extends PanelProps<SimpleOptions> {
    // margin?: {top:number,bottom:number,left:number,right:number},
    // scalezoom?: number
}
// interface Props extends Themeable {
//     // margin?: {top:number,bottom:number,left:number,right:number},
//     // scalezoom?: number
// }
interface DataArrOp extends Array<any> {
    name?: string,
    timestep?: number
    cpus?: number
}
interface States {
    datas: Record<string,any>[],
    dataByTime: DataByTime,
    data:DataObject[],
    // jobs: JobOBject[],
    dimensions: Dimension[],
    selectedService?: number,
    timeStamp: Date[],
    scalezoom?:1,
    margin?: {top:number,bottom:number,left:number,right:number},
    scheme: schemeData,
    drawThreshold: number,
    sankeyData: SankeyInput[],
    LineData: LineInput[],
    LineTime?:Date,
    pieChartSum: {total: number,usage: number,_usage:number}
    pieData: {key: string, value: UserOBject, text:string}[],
    pieOverlay?:{key:string,value:number,parent:string},
    pieDataHighlight?: Record<string, any>,
    colorBy: string,
    displayOpt: DisplayOpt,
    lineaScale: number
    cluster?: Cluster[],
    clusterDescription?: Record<string, any>,
    colorCluster?: Function
    clusterInfo?:Record<string, any>,
    outlyingBins?:OutlierGroup
    radarDataTip?:RadarData[],
    zoomH?:number
    sankeyComputeSelected?:CompSelect,
    clusterOpt: Binopt
}

export interface Dimension {
    range: [number,number],
    rangeMinMax: [number,number],
    rangePredict: [number,number],
    scale: ScaleLinear<any, any>,
    order: number,
    key?: string,
    text: string,
    active: number[],
    tickFormat?: Function,
    angle?:number,
    enable?:boolean
}
export class MainViz extends React.Component<Props, States>{
    svgRef = React.createRef<SVGSVGElement>();
    widthView(){return this.props.width*(this.state.scalezoom??1)}
    heightView(){return this.props.height*(this.state.scalezoom??1)}
    widthG(){return this.widthView()-(this.state.margin??{top:0,bottom:0,left:0,right:0}).left-(this.state.margin??{top:0,bottom:0,left:0,right:0}).right}
    heightG(){return this.heightView()-(this.state.margin??{top:0,bottom:0,left:0,right:0}).top-(this.state.margin??{top:0,bottom:0,left:0,right:0}).bottom}
    centerX(){return (this.state.margin??{top:0,bottom:0,left:0,right:0}).left+this.widthG()/2;}
    centerY(){return (this.state.margin??{top:0,bottom:0,left:0,right:0}).top+this.heightG()/2;}
    r(){return (d3.min([this.props.width, this.props.height])??0) / 2-20}

    public static defaultProps = {
        width:100,
        height:100,
        margin :{top:0,bottom:0,left:0,right:0},
        scalezoom: 1,
        // nodePerRing: 50,
        // nodeSize:5
    }
    state:States = {
            data: [],
            datas: [],
            dataByTime: {},
            dimensions: [],
            selectedService: 0,
            timeStamp:[],
            scheme: {
                    computers: {},
                    emptyMap: {},
                    jobArr: {},
                    jobs: {},
                    minMaxData: {},
                    timespan: [],
                    tsnedata: {},
                    userArr: {},
                    users: {},
                    _userarrdata:{}
                },
        drawThreshold:650 / 700,
        sankeyData:[],
        LineData:[],
        pieData:[],
        pieDataHighlight:undefined,
        pieChartSum: {total: 0,usage: 0,_usage:0},
        colorBy:'name',
        displayOpt:'compute_num',
        lineaScale:0,
        clusterOpt:{clusterMethod:'leaderbin',bin:{startBinGridSize:10,range:[8,9]}},
        }
    colorByName = d3.scaleOrdinal(["#1f77b4", "#ff7f0e", "#2ca02c", "#d62728", "#9467bd", "#8c564b", "#e377c2", "#bcbd22", "#17becf", "#aec7e8", "#ffbb78", "#98df8a", "#ff9896", "#c5b0d5", "#c49c94", "#f7b6d2", "#dbdb8d", "#9edae5"])
    colorByVal = d3.scaleSequential(d3.interpolateGreys).domain([1,-0.25])
    color:ScaleOrdinal<any,string>|ScaleSequential<string>= this.colorByName
    constructor(props:Props) {
        super(props);
    }

    componentDidMount(){
        const opt:Partial<States> = this.handleData();
        if (opt.dimensions)
            opt.selectedService = 0;
        opt.LineData = this.changeLineData(opt); // Line Chart
        this.setState(opt as NonNullable<States>);
        appEvents.emit(AppEvents.alertSuccess, ['Hello']);
    }
    componentDidUpdate(prevProps: Readonly<Props>, prevState: Readonly<States>, snapshot?: any): void {
        if (this.props.data!==prevProps.data){
            const opt:Partial<States> = this.handleData();
            if (opt.dimensions)
                opt.selectedService = 0;
            opt.LineData = this.changeLineData(opt); // Line Chart
            this.setState(opt as NonNullable<States>);
        }
    }
    handleData(){
        console.time('handleData')
        const {data} = this.props;
        const {userEncoded} = this.props.options;
        const dataByTime: DataByTime = {};
        const datas = new Array();
        let nodeLists = new Map();
        const dimensions:Dimension[] = [];
        let dimensionObject: Record<string,Dimension> = {};
        let timeObject: Record<string,Date> = {};
        let coreLimit = this.props.options.coreLimit;
        const scheme: schemeData = {
            computers: {},
            emptyMap: {},
            jobArr: {},
            jobs: {},
            minMaxData: {},
            timespan: [],
            tsnedata: {},
            userArr: {},
            users: {},
            _userarrdata:{}
        }
        function getDim(range: [number,number], order: number, dim: string): Dimension {
            const scale = d3.scaleLinear().domain(range);

            const dimControl: Dimension = {
                range,
                rangeMinMax:[range[0],range[1]],
                rangePredict:[range[0],range[1]],
                scale,
                order,
                text: dim,
                active: new Array<number>(),
                tickFormat: undefined
            };
            return dimControl;
        }

        function handleDataWithname(serie: DataFrame) {
            const dim = serie.refId ?? '';
            const times = serie.fields.find(e => e.name.trim().toLowerCase() === 'time');
            const values = serie.fields.find(e => e.name === 'value');
            const nodeids = serie.fields.find(e => e.name === 'name');
            const dimTarget:( Dimension | undefined ) = dimensionObject[dim];
            let range: [number, number] = dimTarget?[dimTarget.range[0]??Infinity,dimTarget.range[1]??-Infinity]:[Infinity, -Infinity];
            if (nodeids && nodeids.values && times && times.values && values && values.values) {
                const nodeid = nodeids.values.toArray();
                nodeid.forEach((id, i) => {
                    if (!dataByTime.hasOwnProperty(id)) {
                        dataByTime[id] = {};
                    }
                    const val = values.values.get(i);
                    if (!dataByTime[id][times.values.get(i)]) {
                        const item = {time: times.values.get(i),order:0, id};
                        datas.push(item);
                        nodeLists.set(id, true);
                        dataByTime[id][times.values.get(i)] = item;
                    }
                    dataByTime[id][times.values.get(i)][dim] = val;
                    if (val < range[0])
                        range[0] = val;
                    if (val > range[1])
                        range[1] = val;
                })
            }
            if (!dimTarget) // new Dimension
            {
                dimensionObject[dim] = getDim(range, dimensions.length, dim);
                dimensions.push(dimensionObject[dim]);
            }else{
                dimTarget.range = range;
                dimTarget.scale.domain(range);
            }
        }

        function handleDataWithnames(serie: DataFrame) {
            const times = serie.fields.find(e => e.name.trim().toLowerCase() === 'time');

            function extractDimData(id: string, dim: string, d: Field<any, Vector<number>>) {
                if (!dataByTime[id]) {
                    dataByTime[id] = {};
                }
                const dimTarget: (Dimension | undefined) = dimensionObject[dim];
                let range: [number, number] = dimTarget ? [dimTarget.range[0] ?? Infinity, dimTarget.range[1] ?? -Infinity] : [Infinity, -Infinity];
                d.values.toArray().forEach((val:number, i:number) => {
                    if (!dataByTime[id][times?.values.get(i)]) {
                        const item = {time: times?.values.get(i), order: 0, id};
                        datas.push(item);
                        nodeLists.set(id, true);
                        dataByTime[id][times?.values.get(i)] = item;
                    }
                    dataByTime[id][times?.values.get(i)][dim] = val;
                    if (val !== null) {
                        if (val < range[0])
                            range[0] = val;
                        if (val > range[1])
                            range[1] = val;
                    }
                });
                if (!dimTarget) // new Dimension
                {
                    dimensionObject[dim] = getDim(range, dimensions.length, dim);
                    dimensions.push(dimensionObject[dim]);
                } else {
                    dimTarget.range = range;
                    dimTarget.scale.domain(range);
                }
            }

            if (times) {
                serie.fields.filter(e => e.type === 'number').forEach(d => {
                    if(d.labels) {
                        let dim = d.name;
                        let id = '';
                        if (dim==='value'){
                            let str = Object.values(d.labels)[0].split('|').map((d:string)=>d.trim());
                            id = str[0].trim();
                            dim = str[1].trim();
                        }else{
                            id = Object.values(d.labels)[0].replace('|','').trim();
                        }
                        extractDimData(id, dim, d);
                    }else if(d.name.split('|').length===2){
                        let str = d.name.split('|');
                        let id = str[1].trim();
                        let dim = str[0].trim();
                        extractDimData(id, dim, d);
                    }
                })
            }
        }
        function handleNodeJobCore(serie:DataFrame):{computes?:Record<string, {cpus:Record<string, number>[],job_id:string[][]}>,jobs?:Record<string, {start_Index:number,finish_Index:number,node_list_obj:Record<string, number>}>} {
            const time = serie.fields.find(e => e.name.trim().toLowerCase() === 'time');
            const job_lists = serie.fields.find(e => e.name === 'jobs');
            const cpus = serie.fields.find(e => e.name === 'cpus');
            const compute = serie.fields.find(e => e.name === 'name');
            const computes:Record<string, {cpus:Record<string, number>[],job_id:string[][]}>={};
            const jobs:Record<string, {start_Index:number,finish_Index:number,node_list_obj:Record<string, number>}> = {};
            if(time&&job_lists&&cpus&&compute){
                const _time:Record<number,number> = {};
                let ti = 0;
                time.values.toArray().map((t,i)=>{
                    if(!_time[t]){
                        _time[t] = ti;
                        ti++;
                    }
                    const job_idstring = job_lists.values.get(i).replace(/\{|\}/g,'');
                    const job_id = job_idstring===''?[]:job_idstring.split(',').map((d:string)=>d.trim());
                    const cpustring = cpus.values.get(i).replace(/\{|\}/g,'');
                    const cpu = cpustring===''?[]:cpustring.split(',').map((d:string)=>d.trim());
                    const name = compute.values.get(i);
                    if(!computes[name]){
                        computes[name] = {cpus:[],job_id:[]}
                    }
                    computes[name].job_id[ti] = job_id;
                    if(!computes[name].cpus[ti])
                        computes[name].cpus[ti] = {}
                    job_id.forEach((j:string,ji:number)=>{
                        computes[name].cpus[ti][j] = +cpu[ji];
                        if (!jobs[j]){
                            jobs[j] = {start_Index:ti,finish_Index:ti,node_list_obj:{}};
                        }
                        jobs[j].finish_Index = ti;
                        jobs[j].node_list_obj[name] = +cpu[ji];
                    });
                })
                return {computes,jobs};
            }
            return {computes:undefined,jobs:undefined};
        }
        function handleJobData(serie:DataFrame,userEncoded:boolean,job_ref?:Record<string,{start_Index:number,finish_Index:number,node_list_obj:Record<string, number>}>):Record<string, JobOBject> {
            const job_ids = serie.fields.find(e => e.name === 'job_id');
            const job_names = serie.fields.find(e => e.name === 'name');
            const node_lists = serie.fields.find(e => e.name === 'nodes');
            const user_names = serie.fields.find(e => e.name === 'user_name');
            const start_times = serie.fields.find(e => e.name === 'start_time');
            const end_times = serie.fields.find(e => e.name === 'end_time');
            const cpus = serie.fields.find(e => e.name === 'cpus');
            const node_count = serie.fields.find(e => e.name === 'node_count');
            // console.log(serie)
            const jobs:Record<string, JobOBject>={};
            const userEncode:Record<string, string> = {};
            if(user_names){
                if (userEncoded){
                    let userCodecount = 1;
                    user_names.values.toArray().forEach((name,i)=>{
                        if(!userEncode[name]){
                            userEncode[name] = 'user'+userCodecount;
                            userCodecount++;
                        }
                    })
                }else{
                    user_names.values.toArray().forEach((name,i)=>{
                        userEncode[name] = name;
                    })
                }
            }
            if(job_ids&&job_names&&node_lists&&user_names&&start_times&&end_times&&cpus&&node_count){
                job_ids.values.toArray().map((job_id,i)=>{
                    const user_name = userEncode[user_names.values.get(i)];
                    const job_name = job_names.values.get(i);
                    const start_time = start_times.values.get(i)*1000;
                    const end_time = end_times.values.get(i)*1000;
                    const nodestring = node_lists.values.get(i).replace(/\{|\}/g,'');
                    const node_list = nodestring===''?[]:nodestring.split(',').map((d:string)=>d.trim());
                    const cpus_ = (+cpus.values.get(i));
                    const cores = cpus_/(+node_count.values.get(i));
                    let node_list_obj: Record<string, number> = {};
                    if(job_ref&&job_ref[job_id]){
                        node_list_obj = job_ref[job_id].node_list_obj;
                        // const v = Object.values(job_ref[job_id].node_list_obj)
                        // if (v.find(d=>d!==v[0]))
                        //     debugger
                    }else
                        node_list.forEach((n:string)=>node_list_obj[n]=cores);
                    if(node_list.length)
                        jobs[job_id] = {
                            job_id,
                            user_name,
                            job_name,
                            node_list,
                            node_list_obj,
                            _node_list:node_list.slice(),
                            _node_list_obj:{...node_list_obj},
                            cpus:cpus_,
                            cpu_cores:cores,
                            summary: [],
                            start_time,
                            finish_time:end_time
                        }
                });
            }
            return jobs;
        }
        function handleWorkload(cpus:Record<string, Record<string, any>>,times:number[]) {
            const dim = 'workLoad';
            const dimTarget:( Dimension | undefined ) = dimensionObject[dim];
            let range: [number, number] = dimTarget?[dimTarget.range[0]??Infinity,dimTarget.range[1]??-Infinity]:[Infinity, -Infinity];
            if (times) {
                const nodeid = Object.keys(cpus);
                nodeid.forEach((id) => {
                    if (!dataByTime.hasOwnProperty(id)) {
                        dataByTime[id] = {};
                    }
                    times.forEach((ti,i)=>{
                        const val = (cpus[id].cpus && cpus[id].cpus[i])?(Math.min(d3.sum(Object.values(cpus[id].cpus[i])),coreLimit)/coreLimit):0;

                        if (!dataByTime[id][times[i]]) {
                            const item = {time: times[i],order:0, id};
                            datas.push(item);
                            nodeLists.set(id, true);
                            dataByTime[id][times[i]] = item;
                        }
                        dataByTime[id][times[i]][dim] = val;
                        if (val < range[0])
                            range[0] = val;
                        if (val > range[1])
                            range[1] = val;
                    })
                })

            }
            if (!dimTarget) // new Dimension
            {
                dimensionObject[dim] = getDim(range, dimensions.length, dim);
                dimensions.push(dimensionObject[dim]);
            }else{
                dimTarget.range = range;
                dimTarget.scale.domain(range);
            }
        }
        let job_serie = undefined;
        let job_core_serie = undefined;
        data.series.forEach((serie) => {
            const time = serie.fields.find(e=>e.name.trim().toLowerCase()==='time');
            if (time){
                if (serie.fields.find(e=>e.name==='jobs')&&serie.fields.find(e=>e.name==='cpus')){
                    job_core_serie = serie;
                }else {
                    time.values.toArray()
                        .forEach(t => {
                            timeObject[t] = t;
                        });
                    if (serie.fields.find(e => e.name === 'name'))
                        handleDataWithname(serie);
                    else
                        handleDataWithnames(serie)
                }
            }else{
                job_serie = serie;
            }
        });
        let job_ref:Record<string, {start_Index:number,finish_Index:number,node_list_obj:Record<string, number>}>|undefined = undefined;
        if(job_core_serie) {
            const r = handleNodeJobCore(job_core_serie);
            if(r.computes)
                handleWorkload(r.computes,Object.values<Date>(timeObject).map((d:Date)=>+d as number));
            job_ref = r.jobs;
        }
        if(job_serie){
            // let computes:Record<string, {cpus:Record<string, number>[],job_id:string[][]}>|undefined = undefined;
            scheme.jobs = handleJobData(job_serie,userEncoded,job_ref);
        }
        const timeStamp = Object.values<Date>(timeObject).sort((a,b)=>+a - (+b))??[];

        dimensions.sort((a, b) => a.order - b.order).forEach((d,i)=>{
            d.order = i;
            d.angle = (i/dimensions.length)*2*Math.PI;
        });
        const jobs = scheme.jobs;
        //add none value node
        Object.keys(jobs).forEach(k=> {
            const d = jobs[k];
            d._node_list = d.node_list.slice();
            d._node_list.forEach(id => {
                if (!dataByTime[id]) {
                    dataByTime[id] = {};
                    timeStamp.forEach(_t=>{
                        const t = ''+_t;
                        const val = undefined;
                        const item = {time: t,order:0, id};
                        datas.push(item);
                        nodeLists.set(id, true);
                        dataByTime[id][t] = item;
                        dimensions.forEach(dim=>{
                            dataByTime[id][t][dim.text] = val;
                        })
                    })
                }
            });
        });
        const {computers,tsnedata} = this.handleSmalldata(dimensions,dataByTime,timeStamp);

        scheme.computers=computers;
        // remove not exist comp
        Object.keys(jobs).forEach(k=> {
            const d = jobs[k];
            d.node_list = [];
            d._node_list.forEach(comp => {
                if (computers[comp])
                    d.node_list.push(comp);
                else
                    delete d.node_list_obj[comp]
            });
            if(d.node_list.length===0)
                delete jobs[k]
        });
        const {users} = this.handleCUJ(scheme,timeStamp);
        const result = this.handleDataComputeByUser(computers,jobs,timeStamp);
        const  jobCompTimeline = result.data;
        const noJobMap = result.noJobMap;
        const {jobTimeline, jobarrdata,userarrdata,_userarrdata,minMaxDataCompJob,minMaxDataUser} = this.handleDataComputeByJob({tsnedata,computers,jobs,users,timespan:timeStamp});

        
        Object.values(jobs).forEach(d=>{
            d.summary = dimensions.map((s,si)=>{
                const val:SummaryArr = {mean: jobarrdata[d.job_id]?d3.mean<DataArr>(jobarrdata[d.job_id],(d:DataArr)=>d?(d[si] as number):undefined):null,
                min : minMaxDataCompJob[d.job_id]?d3.min(minMaxDataCompJob[d.job_id],d=>d?d[0][si]:undefined):null,
                max : minMaxDataCompJob[d.job_id]?d3.max(minMaxDataCompJob[d.job_id],d=>d?d[1][si]:undefined):null,
                num : jobarrdata[d.job_id].filter(d=>((d?d[si]:undefined) !==undefined)).length}
                return val
            })
        });
        Object.keys(users).forEach(k=>{
            const d = users[k];
            this.colorByName(k);
            d.total_nodes = d.node.length;
            d.summary = dimensions.map((s,si)=>{
                const val = {mean: userarrdata[k]?d3.mean(userarrdata[k],d=>d?d[si]:undefined):null,
                min : minMaxDataUser[k]?d3.min(minMaxDataUser[k],d=>d?d[0][si]:undefined):null,
                max : minMaxDataUser[k]?d3.max(minMaxDataUser[k],d=>d?d[1][si]:undefined):null,
                num : userarrdata[k].filter(d=>((d?d[si]:undefined) !==undefined)).length};
                return val
            })
        });
        const pieChartSum=this.getpieSum.bind(this)(this.state.displayOpt,computers,timeStamp.length)
        Object.keys(computers).forEach(k=>{
            const d = computers[k];
            d.summary = dimensions.map((s,si)=>{
                const val = {mean: tsnedata[k]?d3.mean(tsnedata[k],d=>d?d[si]:undefined):null,
                min : tsnedata[k]?d3.min(tsnedata[k],d=>d?d[si]:undefined):null,
                max : tsnedata[k]?d3.max(tsnedata[k],d=>d?d[si]:undefined):null,
                num : tsnedata[k]?tsnedata[k].filter(d=>((d?d[si]:undefined) !==undefined)).length:0};
                return val
            })
        });

        scheme.users=users;
        scheme.timespan=timeStamp;
        scheme.tsnedata=tsnedata;
        scheme.emptyMap=noJobMap;
        scheme.jobArr=jobarrdata;
        scheme.userArr=userarrdata;
        scheme._userarrdata=_userarrdata;

        const sankeyData = this.changeSankeyData(this.state.displayOpt,{scheme});
        const pieData = Object.keys(scheme.users).map(k=>({key:k,value:scheme.users[k],text:k}));

        const flatv = _.flatten(Object.values(scheme.tsnedata));
        let outlyingBins = outlier(flatv,dimensions)
        let {cluster,clusterDescription,colorCluster,clusterInfo} = calculateCluster(flatv,dimensions,this.state.clusterOpt,()=>{},()=>{})

        console.timeEnd('handleData')
        return ({dimensions,dataByTime,timeStamp,
            datas,jobCompTimeline,jobTimeline,sankeyData,pieData,pieDataHighlight:undefined,
        scheme,pieChartSum,cluster,clusterDescription,colorCluster,clusterInfo,outlyingBins})
    }
    getpieSum(displayOpt:string,computers:Record<string,CompOBject>,timeStep:number){
        const pieChartSum={total: 0,usage: 0,_usage:0};
        if(displayOpt==='core'){
            Object.keys(computers).forEach(k=> {
                const d = computers[k];
                pieChartSum._usage += d3.sum(d.cpus, (e: Record<string, number>) => Math.min(this.props.options.coreLimit, d3.sum(Object.values(e)))) / timeStep;
            })
            pieChartSum.total = this.props.options.coreLimit*Object.keys(computers).length;
        }else{
            Object.keys(computers).forEach(k=> {
                const d = computers[k];
                pieChartSum._usage += d3.sum(d.job_id, (e: string[]) => e.length?1:0) / timeStep;
            })
            pieChartSum.total = Object.keys(computers).length;
        }
        pieChartSum.usage = pieChartSum._usage;
        return pieChartSum;
    }
    changeSankeyData(choice:DisplayOpt,opts:Partial<States>={}){
        const {computers,jobs,timespan:timeStamp} = opts.scheme??this.state.scheme;
        if(choice==='core'){
            return this.handleDataComputeByUser_core({computers,jobs,timespan:timeStamp})
        }else{
            // console.log('compute')
            return this.handleDataComputeByUser_compute({computers,jobs,timespan:timeStamp})
        }
    }
    changePieInfo(choice:DisplayOpt,opts:Partial<States>={}) {
        const {computers, timespan: timeStamp} = opts.scheme ?? this.state.scheme;
        const pieChartSum=this.getpieSum.bind(this)(choice,computers,timeStamp.length);
        return pieChartSum;
    }
    changeLineData(opts:Partial<States>={},lists?:string[],listsJob?:string[]):LineInput[] {  // Line Chart
        const dimensions = opts.dimensions??this.state.dimensions;
        const selectedService = opts.selectedService??this.state.selectedService??0;
        const scheme = opts.scheme??this.state.scheme;
        const userarrdata = scheme.userArr;
        const jobArr = scheme.jobArr;

        // Line Chart
        const timespan = scheme.timespan;
        lists = lists??Object.keys(userarrdata);
        const LineData:LineInput[] = lists.map(k=>({key:k,value:userarrdata[k].map((d):[Date,number|undefined]=>[timespan[d.timestep] as Date,d[selectedService]!==undefined?dimensions[selectedService].scale.invert(d[selectedService]):undefined])}));
        if(listsJob)
            listsJob.forEach(jid=>{
                LineData.push({key:jid,color:'gray',value:jobArr[jid].map((d):[Date,number|undefined]=>[timespan[d.timestep] as Date,d[selectedService]!==undefined?dimensions[selectedService].scale.invert(d[selectedService]):undefined])})
            })
        return LineData;
    }
    onseparate(name:string,isSeperate:boolean){
        if(isSeperate){
            const dimensions = this.state.dimensions;
            const selectedService = this.state.selectedService??0;
            const scheme = this.state.scheme;
            const computers = this.state.scheme.computers;
            const _userarrdata = scheme._userarrdata;

            // Line Chart
            const timespan = scheme.timespan;
            if(!_userarrdata[name])
                this.setState({LineData:[]})
            else {
                const _notShare:[Date,(number|undefined)[]][] = timespan.map((t,ti)=>[t as Date,[]]);
                const _share:[Date,(number|undefined)[]][] = timespan.map((t,ti)=>[t as Date,[]]);

                // const pieData = Object.keys(scheme.users).map(k=>({key:k,value:scheme.users[k],text:k}));
                _userarrdata[name].forEach((d)=>{
                    d.forEach(e=>{
                        if(computers[e.name]&&(computers[e.name].users[e.timestep].length===1)){
                            _notShare[e.timestep][1].push(e[selectedService]!==undefined?dimensions[selectedService].scale.invert(e[selectedService]):undefined)
                        }else{
                            _share[e.timestep][1].push(e[selectedService]!==undefined?dimensions[selectedService].scale.invert(e[selectedService]):undefined)
                        }
                    })
                })
                const notShare:{key:string,value:[Date,(number|undefined)][]} = {key:name,value:_notShare.map(d=>[d[0],d3.mean(d[1])])};
                const share:{key:string,value:[Date,(number|undefined)][]} = {key:'_share_',value:_share.map(d=>[d[0],d3.mean(d[1])])};
                const LineData = [notShare,share]//lists.map(k=>({key:k,value:userarrdata[k].map((d):[Date,number|undefined]=>[timespan[d.timestep] as Date,d[selectedService]!==undefined?dimensions[selectedService].scale.invert(d[selectedService]):undefined])}));
                this.setState({LineData})
            }
        }else {
            const LineData = this.changeLineData({},[name])
            this.setState({LineData})
        }
    }
    handleSmalldata(dimensions:Dimension[],dataByTime:DataByTime,timeStamp:Date[]){
        const tsnedata:TSNEDATA = {};
        const computers:Record<string,CompOBject> ={};
        Object.keys(dataByTime).forEach(comp=>{
            const compData = dataByTime[comp];
            tsnedata[comp] = [];
            computers[comp]={
                job_id: [],
                key: comp,
                user: [],
                drawData:[]
            }
            dimensions.forEach(dim=> computers[comp][dim.text]=[]);
            timeStamp.forEach((t,ti)=>{
                const current = compData[''+t];
                const d: DataArrOp = dimensions.map(dim=>{
                    if(current){
                        computers[comp][dim.text][ti] = current[dim.text];
                        const val = current[dim.text];
                        return (val!==null) ? dim.scale(val as number):undefined;
                    }
                    return null
                });
                d.name = comp;
                d.timestep = ti;
                tsnedata[comp][ti] = d as DataArr;
            });

        })
        return {computers,tsnedata}
    }
    handleDataComputeByJob(input:Partial<schemeData>){
        const tsnedata = input.tsnedata??{};
        const computers:Record<string, CompOBject>=input.computers??{};
        const _jobs:Record<string, JobOBject>=input.jobs??{};
        const _users:Record<string, UserOBject> = input.users??{};
        const jobs:Record<string, JobOBject> = {};
        const users:Record<string, UserOBject> = {};
        const jobarrdata:Record<string, DataArr[]> = {};
        const _jobarrdata:Record<string, DataArr[][]> = {};
        const userarrdata:Record<string, DataArr[]> = {};
        const _userarrdata:Record<string, DataArr[][]> = {};
        const minMaxDataCompJob:Record<string, [DataArr,DataArr][]> = {};
        const minMaxDataUser:Record<string, [DataArr,DataArr][]> = {};
        const timespan:Date[] = input.timespan??[];
        Object.keys(_jobs).forEach(k=>{
            // if (_jobs[k].total_nodes>1){
            jobs[k] = _jobs[k];
            _jobarrdata[k] = [];
            jobarrdata[k] = [];
            minMaxDataCompJob[k] = [];

            if (!jobs[k].isJobarray){
                users[_jobs[k].user_name] = _users[_jobs[k].user_name];
                userarrdata[_jobs[k].user_name] = [];
                _userarrdata[_jobs[k].user_name] = [];
                minMaxDataUser[_jobs[k].user_name] = [];
            }else{
                jobs[k].comp = {};
            }
            // }
        });

        let data = [];
        for (let comp in computers){
            interface Item_inside {key:string,values:any,value:number|undefined}
            interface Item extends Record<string,any>{
                key:string,values:(Item_inside[]| null)[],range:[number,number],data:CompOBject
            }
            let item:Item = {key:comp,values:[] ,range:[Infinity,-Infinity],data:computers[comp]};
            let job:Record<string,boolean> = {};
            computers[comp].job_id.forEach((jIDs,i)=>{
                let jobArr = jIDs.map(j=>jobs[j]).filter(d=>{
                    if (d){
                        if (!_jobarrdata[d.job_id][i]){
                            const empty:DataArrOp = [];
                            empty.name='';
                            empty.timestep=0;
                            _jobarrdata[d.job_id][i]=(empty as DataArr);
                        }
                        const tsne_comp = tsnedata[comp];
                        if(tsne_comp)
                            _jobarrdata[d.job_id][i].push(tsne_comp[i]);
                        if (!_userarrdata[d.user_name][i]){
                            const empty:DataArrOp = [];
                            empty.name='';
                            empty.timestep=0;
                            empty.cpus=0;
                            _userarrdata[d.user_name][i] =(empty as DataArr);
                        }
                        if(tsne_comp)
                            _userarrdata[d.user_name][i].push(tsne_comp[i]);

                        if(d.job_array_id){
                            if (!_jobarrdata[d.job_array_id][i]){
                                const empty:DataArrOp = [];
                                empty.name='';
                                empty.timestep=0;
                                _jobarrdata[d.job_array_id][i]=(empty as DataArr);
                            }
                            if (!jobs[d.job_array_id].comp[i])
                                jobs[d.job_array_id].comp[i] = {};
                            if (!jobs[d.job_array_id].comp[i][comp]){
                                jobs[d.job_array_id].comp[i][comp] = 1;
                                _jobarrdata[d.job_array_id][i].push(tsnedata[comp][i]);
                            }
                        }
                        return true;
                    }
                    return false
                });
                const key = jobArr.map(d=>d.job_id).toString();
                if (!job[key])
                    job[key]=true;
                else
                    jobArr=[];

                if (jobArr.length){
                    let username = d3.nest<JobOBject,number>().key(d=>d.job_id)
                        .rollup(d=>d3.sum<JobOBject>(d,(e:JobOBject)=>e.node_list_obj[comp])).entries(jobArr);
                    // username.total = d3.sum(username,e=>e.value)
                    username.sort((a,b)=>d3.ascending(a.key,b.key))
                    item.values.push(username);
                }else
                    item.values.push(null);
                item[''+timespan[i]] = item.values[i];
            });
            data.push(item);
        }
        Object.keys(_jobarrdata).forEach(k=>{
            _jobarrdata[k].forEach((d,i)=>{
                const timestep = _jobarrdata[k][i][0].timestep;
                let valueMin:DataArrOp= [];
                let valueMax:DataArrOp= [];
                let value:DataArrOp = _jobarrdata[k][i][0].map((d,si)=>{
                    const vals = _jobarrdata[k][i].map(d=>d[si]);
                    valueMin.push(d3.min(vals));
                    valueMax.push(d3.max(vals));
                    let sum = 0;
                    let total = 0;
                    _jobarrdata[k][i].forEach(d=>{
                        if(d[si]!==undefined) {
                            const val = computers[d.name].cpus[i][k];
                            sum += d[si] * val;
                            total += val;
                        }
                    });
                    // if(total&& _.isNaN(sum/total))
                    //     debugger
                    if(!total)
                        return undefined;
                    else
                        return sum/total;
                    // return  d3.mean(vals);
                });
                value.name = k;
                valueMin.name = k;
                valueMax.name = k;
                value.timestep = timestep;
                valueMin.timestep = timestep;
                valueMax.timestep = timestep;
                minMaxDataCompJob[k][i] = [valueMin as DataArr,valueMax as DataArr]
                jobarrdata[k][i] = (value as DataArr);
            })
        })

        Object.keys(_userarrdata).forEach(k=>{
            _userarrdata[k].forEach((d,i)=>{
                const timestep = _userarrdata[k][i][0].timestep;
                let valueMin:DataArrOp = [];
                let valueMax:DataArrOp = [];
                let value:DataArrOp = _userarrdata[k][i][0].map((d,si)=>{
                    const vals = _userarrdata[k][i].map(d=>d[si]);
                    valueMin.push(d3.min(vals));
                    valueMax.push(d3.max(vals));
                    return  d3.mean(vals);
                });
                value.name = k;
                valueMin.name = k;
                valueMax.name = k;
                value.timestep = timestep;
                valueMin.timestep = timestep;
                valueMax.timestep = timestep;
                minMaxDataUser[k][i] = [valueMin as DataArr,valueMax as DataArr]
                userarrdata[k][i] = (value as DataArr);
            })
        })
        data.sort((a,b)=>+a.range[0]-b.range[0])
        return {jobTimeline:data, jobarrdata,userarrdata,_userarrdata,minMaxDataCompJob,minMaxDataUser};
    }
    handleDataComputeByUser(computers:Record<string, CompOBject>,jobs:Record<string,JobOBject>,timeStamp:Date[]){
        //start
        let data = [];
        interface OBJ extends Array<{key:string,type:string,value:number}>{
            total?:number
        }
        let obj:Record<string,{key:string,values:(OBJ|null)[],range:[number,number],data:JobOBject, arr:{time:Date,value:[any,any]}[]}> = {};
        let noJobMap:Record<string,any> = {};
        for (let j in jobs){
            obj[j] = {key:j,values:timeStamp.map(t=>null as OBJ|null),range:[Infinity,-Infinity],data:jobs[j],arr:[]};
            data.push(obj[j]);
        }
        for (let comp in computers){
            let jonj:Record<string, boolean> = {};
            noJobMap[comp] = [];
            computers[comp].job_id.forEach((jIDs,i)=>{
                if (jIDs.length){
                    jIDs.forEach(j=>{
                        if (!jonj[j]){
                            let item = obj[j];
                            if (!item.values[i]) {
                                item.values[i] = [] as OBJ;
                                // @ts-ignore
                                item.values[i].total = 0;
                            }
                            const compData = {key:comp,type:'compute',value:1};
                            const userData = {key:jobs[j].user_name,type:'user',value:1};
                            const current = item.values[i];
                            if(current!==null && current.length){
                                current.push(compData);
                                // item.values[i].push(userData);
                                current.total = (current.total??0) + jobs[j].node_list_obj[comp];
                                // item.arr.push({time:Layout.timespan[i],value:[compData,{key:j,type:'job',value:1},userData]});
                                item.arr.push({time:timeStamp[i],value:[{key:j,type:'job',value:1},userData]});
                                item.arr.push({time:timeStamp[i],value:[compData,{key:j,type:'job',value:1}]});
                                if(jobs[j].job_array_id){
                                    // item.arr.push({time:Layout.timespan[i],value:[compData,{key:jobs[j].job_array_id,type:'job',value:1},userData]});
                                    item.arr.push({time:timeStamp[i],value:[compData,{key:jobs[j].job_array_id,type:'job',value:1}]});
                                    item.arr.push({time:timeStamp[i],value:[{key:jobs[j].job_array_id,type:'job',value:1},userData]});
                                }
                            }
                            jonj[j]=true;
                        }
                    });
                }else{
                    noJobMap[comp][i] = 1;
                }
            });
        }
        // data.sort((a,b)=>+a.range[0]-b.range[0])
        return {data,noJobMap};
    }
    // @ts-ignore
    handleCUJ({computers,jobs},timestamp){
        // const users:Record<string, UserOBject> = {};
        const compute_user: Record<string, Record<string, any>[]> = {}
        Object.keys(computers).forEach((k:string)=>{
            const d = computers[k];
            d.user=[] as string[];
            d.jobName=timestamp.map(()=>[]);
            d.job_id=timestamp.map(()=>[]);
            d.users=timestamp.map(()=>[]);
            d.cpus=timestamp.map(()=>({}));
            compute_user[k] = timestamp.map(()=>({}));
        });

        const jobData = d3.entries<JobOBject>(jobs);
        jobData.forEach(j=>{
            j.value.start_Index = timestamp.findIndex((t:Date)=>t>j.value.start_time);
            if (j.value.start_Index>0)
                j.value.start_Index --;
            else if(j.value.start_Index ===-1)
                j.value.start_Index = timestamp.length-1;
            j.value.finish_Index = j.value.finish_time?timestamp.findIndex((t:Date)=>t>j.value.finish_time):-1;
            if (j.value.finish_Index>0)
                j.value.finish_Index --;
            else if(j.value.finish_Index ===-1)
                j.value.finish_Index = timestamp.length-1;
            j.value.node_list.forEach(comp=>{
                if(computers[comp]){
                    for (let t=j.value.start_Index;t<=j.value.finish_Index;t++){
                        computers[comp].job_id[t].push(j.key);
                        computers[comp].cpus[t][j.key] = (j.value.node_list_obj[comp]);
                        if(!compute_user[comp][t][j.value.user_name]){
                            compute_user[comp][t][j.value.user_name]=true;
                            computers[comp].users[t].push(j.value.user_name);
                        }
                    }
                }
            })
        });
        const users = (this.state.displayOpt==='core')?this.getUsers_core(jobs,computers,timestamp.length):this.getUsers_compute(jobs,computers,timestamp.length);
        Object.keys(users).forEach((k,i)=>{
            users[k].node.forEach((c:any)=> computers[c]?computers[c].user.push(k):'');
        });
        return {users}
    }
    handleDataComputeByUser_compute({computers,jobs,timespan}:{computers:Record<string, CompOBject>,jobs:Record<string, JobOBject>,timespan:Date[]}){
        let data = [];
        for (let comp in computers){
            let item:Record<string, any> = {key:comp,data:computers[comp]};
            computers[comp].job_id.forEach((jIDs,i)=>{
                if (jIDs.length){
                    let jobArr = jIDs.map(j=>jobs[j]);
                    let username:any = d3.nest<JobOBject,number>().key(d=>d.user_name)
                        .rollup(d=>1).entries(jobArr);
                    username.total = d3.sum<UserOBject>(username,e=>e.value);
                    username.jobs = [jIDs,jobArr];
                    item[''+timespan[i]] = username.sort((a:any,b:any)=>d3.ascending(a.key,b.key));
                }else
                    item[''+timespan[i]] = null;
            });
            data.push(item);
        }
        return data as SankeyInput[];
    }
    handleDataComputeByUser_core({computers,jobs,timespan}:{computers:Record<string, CompOBject>,jobs:Record<string, JobOBject>,timespan:Date[]}){
        let data = [];
        for (let comp in computers){
            let item:Record<string, any> = {key:comp,data:computers[comp]};
            computers[comp].job_id.forEach((jIDs,i)=>{
                if (jIDs.length){
                    let jobArr = jIDs.map(j=>jobs[j]);
                    let username:any = d3.nest<JobOBject,number>().key(d=>d.user_name)
                        .rollup(d=>d3.sum<JobOBject>(d,(e)=>e.node_list_obj[comp]??0)).entries(jobArr);
                    username.total = d3.sum<UserOBject>(username,e=>e.value);
                    username.jobs = [jIDs,jobArr];
                    item[''+timespan[i]] = username.sort((a:any,b:any)=>d3.ascending(a.key,b.key));
                }else
                    item[''+timespan[i]] = null;
            });
            data.push(item);
        }
        return data as SankeyInput[];
    }
    onMouseOverSankey(d:Link,jobLink?:{name:string,x:number,y:number,time:Date,layer:number}){
        const {sankeyData,pieChartSum,scheme,dimensions} = this.state;
        const timestep = jobLink?jobLink.layer:d.source.layer;
        const time = jobLink?jobLink.time:d.source.time;
        const _data: Record<string, JobOBject> = {};
        pieChartSum.usage = 0;
        const radarData: RadarData[] = [];
        sankeyData.forEach(comp=>{
            if (comp[time]){
                const compData = (comp[time] as CompOBject);
                compData.jobs[0].forEach((jid: any,ji:number)=>{
                    _data[jid] = {...compData.jobs[1][ji],start_Index:timestep,finish_Index:timestep};
                });
                pieChartSum.usage+=(this.state.displayOpt==='core')?Math.min(compData.total,this.props.options.coreLimit):compData?1:0;

                // radar
                if (d.byComp_t&&d.byComp_t[comp.key]) {
                    const item: RadarData = dimensions.map((s, si) => ({
                        axis: s.text,
                        value: scheme.tsnedata[comp.key][timestep][si],
                        angle: s.angle as number
                    }))
                    item.id = 'tooltipRadarcurrent' + comp.key;
                    item.cluster = scheme.tsnedata[comp.key][timestep].cluster;
                    radarData.push(item);
                }
            }
        });
        d3.select('#pieChartTime').text(time.toLocaleString());
        const _user = (this.state.displayOpt==='core')?this.getUsers_core(_data,this.state.scheme.computers):this.getUsers_compute(_data,this.state.scheme.computers);
        // if(d.source.element.lenth===1){
        //     const userName = d.source.element[0].key;
        //     scheme.users[userName].node.forEach(comp=>{
        //         scheme.computers[comp].cpu[timestep]
        //     })
        // }
        // Line Chart
        const LineData = this.changeLineData({},d._source.map(e=>e.key),jobLink?[jobLink.name]:undefined)
        const node:Record<string,any> = {};
        d.source.element.forEach((e:any)=>node[e.key]=1);
        let pieOverlay = undefined;
        if(jobLink){
            const value=(this.state.displayOpt==='core')?d3.sum(Object.values(this.state.scheme.jobs[jobLink.name].node_list_obj)):Object.keys(this.state.scheme.jobs[jobLink.name].node_list_obj).length;
            pieOverlay = {key:jobLink.name,value:value,parent:this.state.scheme.jobs[jobLink.name].user_name}
        }
        debugger
        this.setState({pieData: Object.keys(_user).map(k=>({key:k,value:_user[k],text:k})),
            pieDataHighlight:{node,time,data:d},pieChartSum,LineData,LineTime:time,pieOverlay,radarDataTip:radarData})
        // userPie.highlight(d.source.element.map(e=>e.key));
    }
    onMouseLeaveSankey(d:Link){
        const {scheme,pieChartSum} = this.state;
        pieChartSum.usage = pieChartSum._usage;
        // Line Chart
        const LineData = this.changeLineData();
        this.setState({pieData: Object.keys(scheme.users).map(k=>({key:k,value:scheme.users[k],text:k}))
        , pieDataHighlight:undefined,pieChartSum,LineData,LineTime:undefined,pieOverlay:undefined,radarDataTip:undefined})
        // userPie.highlight(d.source.element.map(e=>e.key));
    }
    getUsers_core(jobs: Record<string,JobOBject>,computers: Record<string, CompOBject>,timeStep=1){
        const user_job = d3.nest()
            .key((d:any)=>(d.value as JobOBject)['user_name']) //user
            .key((d:any)=>(d.key as string).split('.')[0].trim()) //job array
            .object(d3.entries(jobs));
        const users:Record<string, UserOBject> = {};
        Object.keys(user_job).forEach((k,i)=>{
            const  u =user_job[k];
            const job:string[] = [];
            let totalCore = 0;
            let totalCore_share = 0;
            let totalCore_notShare = 0;
            // @ts-ignore
            const node = _.uniq(_.flattenDeep(_.values(u).map(e=>e.map(d=>{
                job.push(d.key);
                const core = d.value.cpus*(d.value.finish_Index-d.value.start_Index+1);
                totalCore += core;
                if (d.value.finish_Index===d.value.start_Index){
                    d.value.node_list.forEach((comp:string)=>{
                        // if(computers[comp].users[d.value.start_Index].length===1){
                        //     totalCore_notShare += d.value.cpu_cores;
                        // }else{
                        //     totalCore_share += d.value.cpu_cores;
                        // }
                        if(computers[comp].users[d.value.start_Index].length===1){
                            totalCore_notShare += d.value.node_list_obj[comp];
                        }else{
                            totalCore_share += d.value.node_list_obj[comp];
                        }
                    })
                }
                return d.value.node_list;
            }))));
            totalCore = totalCore/timeStep;
            totalCore_notShare = totalCore_notShare/timeStep;
            totalCore_share = totalCore_share/timeStep;
            // const node = _.uniq(_.flattenDeep(_.values(u).map(d=>d.map(d=>(job.push(d.key),totalCore+=d3.sum(Object.values(d.value.node_list_obj)),d.value.node_list)))));
            users[k]={node,job,totalCore,totalCore_notShare,totalCore_share}
        });
        return users;
    }
    getUsers_compute(jobs: Record<string,JobOBject>,computers: Record<string, CompOBject>,timeStep=1){
        const user_job = d3.nest()
            .key((d:any)=>(d.value as JobOBject)['user_name']) //user
            .key((d:any)=>(d.key as string).split('.')[0].trim()) //job array
            .object(d3.entries(jobs));
        const users:Record<string, UserOBject> = {};
        Object.keys(user_job).forEach((k,i)=>{
            const  u =user_job[k];
            const job:string[] = [];
            let totalCore = 0;
            const ob:Record<string,{start:number,end:number,share:number,notShare:number}>={};
            // @ts-ignore
            const node = _.uniq(_.flattenDeep(_.values(u).map(d=>d.map(d=>{
                job.push(d.key);
                d.value.node_list.forEach((com:string)=>{
                    if(!ob[com])
                        ob[com]={start:d.value.start_Index,end:d.value.finish_Index,share:0,notShare:0};
                    else{
                        if(ob[com].start>d.value.start_Index)
                            ob[com].start = d.value.start_Index;
                        if(ob[com].end<d.value.finish_Index)
                            ob[com].end = d.value.finish_Index;
                    }
                    if (d.value.finish_Index===d.value.start_Index){
                        // if(computers[com].users[d.value.start_Index].length===1){
                        //     ob[com].notShare += d.value.cpu_cores;
                        // }else{
                        //     ob[com].share += d.value.cpu_cores;
                        // }
                        if(computers[com].users[d.value.start_Index].length===1){
                            ob[com].notShare = 1//d.value.node_list_obj[com];
                        }else{
                            ob[com].share = 1//d.value.node_list_obj[com];
                        }
                    }
                });
                return d.value.node_list;
            }))));
            // const node = _.uniq(_.flattenDeep(_.values(u).map(d=>d.map(d=>(job.push(d.key),totalCore+=d3.sum(Object.values(d.value.node_list_obj)),d.value.node_list)))));
            totalCore = d3.sum(Object.values(ob),d=>d.end-d.start+1)/timeStep;
            const totalCore_share = d3.sum(Object.values(ob),d=>d.share)/timeStep;
            const totalCore_notShare = d3.sum(Object.values(ob),d=>d.notShare)/timeStep;
            users[k]={node,job,totalCore,totalCore_share,totalCore_notShare}
        });
        return users;
    }
    onColorModeChange(val:string){
        const opt: Partial<States> = {}
        if(val==='name'){
            this.color = this.colorByName;
        }else{
            this.color = this.colorByVal;
            opt.selectedService = +val;
            if(this.state.pieDataHighlight)
                opt.LineData = this.changeLineData({selectedService:+val},this.state.pieDataHighlight.data._source.map((e:any)=>e.key));
            else
                opt.LineData = this.changeLineData({selectedService:+val});
        }
        opt.colorBy = val;
        this.setState(opt as NonNullable<States>)
    }
    onComputeSelected(events?:RadarData[],group?:any){
        if (events){
            const {scheme} = this.state;
            // const nest = d3.nest<RadarData>().key(d=>d.nodeName).entries(events);
            const groupByUser:Record<string, CompSelectNode> = {};
            const groupUser:Record<string, Record<string,CompSelectNode>> = {};
            const comps:Record<string, any> = {};
            events.forEach(d=>{
                const user = getUserName2(scheme.computers[d.nodeName].users[d.timestep]);
                if (!groupByUser[user+'_'+d.timestep])
                    groupByUser[user+'_'+d.timestep] = {user,timestep:d.timestep,comp:[],value:0};
                groupByUser[user+'_'+d.timestep].comp.push(d.nodeName);
                groupByUser[user+'_'+d.timestep].value ++;
                if (!groupUser[user])
                    groupUser[user]={};
                groupUser[user][d.timestep] = groupByUser[user+'_'+d.timestep];
                if (!comps[d.nodeName])
                    comps[d.nodeName] = [];
                comps[d.nodeName].push(groupByUser[user+'_'+d.timestep])
            });
            const links:{key: string, value: CompSelectNode[]}[] = [];
            Object.values(comps).forEach(d=>{
                if (d.length>1){
                    links.push(d)
                }
            });
            this.setState({sankeyComputeSelected:{nodes:groupUser,links,group}})
        }else{
            this.setState({sankeyComputeSelected:undefined});
        }
    }
    render(){
        // const styles = getStyles();
        const {width, height} = this.props;
        const {dimensions,LineData,LineTime,colorBy,displayOpt,colorCluster,cluster,scheme,clusterDescription,
            outlyingBins,radarDataTip,zoomH=1,sankeyComputeSelected} = this.state;
        const selectedService = this.state.selectedService??0;
        const timespan = this.state.scheme.timespan;
        const timeLimit:[Date,Date] = [timespan[0] as Date,timespan[timespan.length-1] as Date];
        const getMetric=(v:any)=>{
            return colorBy!=='name'?((colorBy!=='cluster')?`${dimensions[selectedService].text}: ${+d3.format('.2f')(dimensions[selectedService].scale.invert(v))}`:(clusterDescription?(clusterDescription[v]??'mixed'):'')):''
        };
        return <ThemeContext.Consumer>{(theme) =>dimensions[0]?<div style={{width:width+' px', height:height+' px', position:'relative'}}>
            <Sankey width={width} height={height*zoomH} data={this.state.sankeyData}
                    lineaScale={this.state.lineaScale}
                   timespan={this.state.scheme.timespan}
                   mode={this.state.displayOpt}
                   maxPerUnit={this.state.displayOpt==="core"?this.props.options.coreLimit:1}
                   color={this.color}
                   colorByName={this.colorByName}
                   colorCluster={colorCluster}
                   colorBy={colorBy}
                   getMetric={getMetric}
                   metrics = {this.state.scheme.tsnedata}
                   mouseOver = {this.onMouseOverSankey.bind(this)}
                   mouseLeave = {this.onMouseLeaveSankey.bind(this)}
                    sankeyComputeSelected = {sankeyComputeSelected}
                               theme={theme}/>
            <Drawer open={true} className={css`
      position: absolute;
      top: 0;
      left: 0;
      height: ${height}px;
      max-width: 450px;
    `}>
        <InlineField label="Visualizing">
            <Select
                onChange={(event)=>{const val = event.value??'core';
                this.setState({displayOpt:val,sankeyData:this.changeSankeyData(val),pieChartSum:this.changePieInfo.bind(this)(val,{})})}}
                value={displayOpt}
                options={[
                    { value: 'core', label: '#Cores' },
                    { value: 'compute_num', label: '#Computes' },
                ]}/>
        </InlineField>
        <InlineField label="Color Sankey by:">
            <Select
                onChange={(event)=>this.onColorModeChange(event.value??'name')}
                value={colorBy}
                options={[
                    { value: 'name', label: 'User name' },
                    { value: 'cluster', label: 'Dataset cluster' },
                    ...dimensions.map(d=>({value: `${d.order}`, label: d.text}))
                ]}/>
        </InlineField>
        <InlineField label="Sankey thickness">
            <Slider min={0} step={0.1} max={4} value={this.state.lineaScale} onChange={(selectedIndex:number)=>{this.setState({lineaScale: selectedIndex})}}/>
        </InlineField>
        <InlineField label="Zoom vertical">
            <Slider min={1} step={0.1} max={10} value={zoomH} onChange={(selectedIndex:number)=>{this.setState({zoomH: selectedIndex})}}/>
        </InlineField>
        <h5 style={{textAlign:'center'}}>{displayOpt==='core'?'Core':'Compute'} usage</h5>
        <h6 style={{textAlign:'center'}}>100% = {Math.round(this.state.pieChartSum.total)} {this.state.displayOpt==='core'?'cores':'computes'} | Current usage: {Math.round(this.state.pieChartSum.usage)}</h6>
                <PieChart width={400} height={250}
                          theme={theme}
                    color={this.colorByName}
                      data={this.state.pieData}
                          overlay={this.state.pieOverlay}
                          onseperate={(name:string,isSeperate:boolean)=>{this.onseparate(name,isSeperate)}}
                          highlight = {this.state.pieDataHighlight}
                          refValue={this.state.pieChartSum.usage}
                          maxValue={this.state.pieChartSum.total}
                />
                <br/>
                {/*<label>Line chart by:</label>*/}
                {/*<select*/}
                {/*    onChange={(event)=>{this.setState({selectedService:+event.target.value,LineData:this.changeLineData({selectedService:+event.target.value})})}}>*/}
                {/*    {dimensions.map(d=><option key={d.text} value={d.order}>{d.text}</option>)}*/}
                {/*</select>*/}
                {/*{colorBy!=='name'?(dimensions[selectedService]?<LineChart width={300} height={150} theme={theme}*/}
                {/*           yRange={dimensions[selectedService].range}*/}
                {/*           data={LineData} color={this.colorByName}*/}
                {/*            mark={LineTime}*/}
                {/*/>:<h6 style={{textAlign:'center'}}>No data for this dimension</h6>):''}*/}

                {dimensions[selectedService]?<>
                    <h6 style={{textAlign:'center'}}>{dimensions[selectedService].text}</h6>
                    <LineChart width={400} height={150} theme={theme}
                               xRange={timeLimit}
                              yRange={dimensions[selectedService].range}
                              data={LineData} color={this.colorByName}
                              mark={LineTime}
                /></>:''}
                {
                    radarDataTip?<div style={{background:'white'}}><Radar width={400} height={160} margin={{l:50,b:20,t:20,r:50}} data={radarDataTip} showAxis
                                             fill={'none'} strokeWith={1} stroke={colorCluster?radarDataTip.map(d=>colorCluster(d.cluster)):'#fff'}/></div>:''
                }
                {(colorBy==='cluster')&&<><ClusterControl onSubmit={(opt:Binopt)=>{
                    const state:Partial<States> = calculateCluster(_.flatten(Object.values(scheme.tsnedata)),dimensions,opt,()=>{},()=>{});
                    state.clusterOpt = opt;
                    this.setState(state as NonNullable<States>)
                }}/>
                <p>
                    Cluster inputdata: {this.state.clusterInfo?.input} ({d3.format(",.1%")(this.state.clusterInfo?.input/this.state.clusterInfo?.total)})<br/>
                    Cluster calculation time: {Math.round(this.state.clusterInfo?.clusterCalTime??0)} ms <br/>
                    Total MSE: {d3.format('.2f')(this.state.clusterInfo?.totalMSE??0)}
                </p>
                {
                    cluster&&cluster.map((c,i)=><div key={c.labels} style={{display:'inline-block'}}>
                        <div style={{position:'relative'}}>
                            <Radar width={120} height={120} data={[c.__metrics]} area={true} mean={true} meanColor={"rgba(0,0,0,0.52)"}
                                                   fill={colorCluster?colorCluster(c.name):'#fff'} stroke={'match'}/>
                            <div style={{position:'absolute',top:0,left:0}}>
                                <Radar width={120} height={120} data={[c.__metrics]} compact={true}
                                   fill={'none'} stroke={"black"} strokeWidth={2}/>
                             </div>
                           <p style={{position:'absolute',top:10,left:0, width:'100%',textAlign:'center',color:'black', fontSize:12}}>{c.arr.length}<br/>{i?'':' temporal instances'}</p>
                    </div> </div>)
                }
                {
                    cluster?<div style={{backgroundColor:theme.isDark?'rgba(255,255,255,0.12)':'rgba(0,0,0,0.12)',padding:5, borderRadius:10, border:'1px dashed gray'}}>
                        <h5>Other color for cluster:</h5>
                        <div style={{display:'flex',alignItems:'center',padding:3}}><div style={{marginRight:5,width:20,height:20, borderRadius:10, backgroundColor:colorCluster?colorCluster('outlier'):'#fff'}}/> Outlier</div>
                        <div style={{display:'flex',alignItems:'center',padding:3}}><div style={{marginRight:5,width:20,height:20, borderRadius:10, backgroundColor:colorCluster?colorCluster('missing'):'#fff'}}/> Missing dimension data</div>
                        <div style={{display:'flex',alignItems:'center',padding:3}}><div style={{marginRight:5,width:20,height:20, borderRadius:10, backgroundColor:'white'}}/> Multiple Clusters</div>
                    </div>:''
                }

                <div style={{width:'100%',borderTop:'gray 1px solid', marginTop:10, marginBottom:5}}></div>
                <h5>Outliers: {outlyingBins?Object.keys(outlyingBins.pointObject).length:0} temporal instances</h5>
                {
                    outlyingBins&&outlyingBins.map((c,i)=><div key={c.labels} style={{display:'inline-block'}}>
                        <div style={{position:'relative',opacity:sankeyComputeSelected?(sankeyComputeSelected.group===c.labels?1:0.5):1}}>
                            <Radar width={120} height={120} data={c.arr.map(d=>outlyingBins.pointObject[d.name])}
                                                   fill={'none'} stroke={colorCluster?colorCluster('outlier'):'#fff'}
                                   onMouseOver={(data:any)=>this.onComputeSelected(data,c.labels)}
                                   onMouseLeave={this.onComputeSelected.bind(this)}
                            />
                            {/*<div style={{position:'absolute',top:0,left:0}}>*/}
                            {/*    <Radar width={120} height={120} data={[c.__metrics]} compact={true}*/}
                            {/*       fill={'none'} stroke={"black"} strokeWidth={2}/>*/}
                            {/* </div>*/}
                            <p style={{position:'absolute',top:10,left:0, width:'100%',textAlign:'center',color:'black', fontSize:12}}>{c.arr.length}<br/>
                                {shortArray(Object.keys(c.compObject))}
                            </p>
                    </div> </div>)
                }
                <div style={{width:'100%',borderTop:'gray 1px solid', marginTop:10, marginBottom:5}}></div>
                <h5>Missing dimension data: {outlyingBins?Object.keys(outlyingBins.missingData).length:0} temporal instances</h5>
                </>}
    </Drawer>
    </div>:'No data'}</ThemeContext.Consumer>
    }
}
function shortArray(arr:string[],limit=2){
    return arr.length>limit?(arr.slice(0,limit).join(',')+`, +${arr.length-limit} more`): arr.join(',')
}
