export class BinaryBinner {
    constructor(points, dimension) {
        //TODO: Should check if there are more than 3 unique values here or even after the binning.
        //TODO: May need to clone the points to avoid modifying it, but we don't do to reserve other data or to make the process faster
        // //Clone these to avoid modifying them
        // this.points = points.map(p=>p.slice(0));
        this.points = points;
        this.dimension = dimension;
    }

    get leaders() {
        let self = this;
        let theLeaders = {};
        //find all the leaders
        this.points.forEach(point => {
            const binarypoint = point.map(d=>Math.round(d));
            const binarykey = binarypoint.join('');
            if (!theLeaders[binarykey]) {
                let newLeader = [];
                newLeader.val = binarypoint;
                theLeaders[binarykey] = newLeader;
            }
            theLeaders[binarykey].push(point)
        });

        return _.values(theLeaders);
    }
}